* {
  margin: 0;
  padding: 0;
}

.header {
  margin: 1.5rem 1.5rem 0 1.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__hamburger {
  width: 2rem;
  height: 2rem;
  color: #D0D6F9;
  cursor: pointer;
  z-index: 2;
}

.header__nav {
  position: fixed;
  width: 16rem;
  height: 100vh;
  top: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  background-color: transparent;
  -webkit-backdrop-filter: blur(5rem);
          backdrop-filter: blur(5rem);
  padding-top: 5rem;
  padding-left: 2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 1;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.header__nav__tab {
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2.7px;
  margin-bottom: 2rem;
  color: white;
  list-style: none;
}

.header__nav__tab__index {
  display: inline-block;
  width: 1.5rem;
}

.active {
  font-weight: bold;
  color: #D0D6F9;
}

.home {
  margin-top: 5rem;
}

.home__text {
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 1rem;
  margin: 0 1.5rem;
}

.home__text__h2 {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2.7px;
  color: #D0D6F9;
  text-align: center;
  font-weight: normal;
}

.home__text__h1 {
  font-family: 'Bellefair', serif;
  font-size: 80px;
  text-transform: uppercase;
  font-weight: normal;
  line-height: 100px;
  text-align: center;
}

.home__text__h3 {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 1rem;
  text-transform: initial;
  text-align: center;
  line-height: 25px;
  color: #D0D6F9;
  font-weight: normal;
}

.home__circle {
  width: 150px;
  height: 150px;
  position: fixed;
  border-radius: 50%;
  background-color: white;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: 0;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  display: -ms-grid;
  display: grid;
  place-items: center;
  cursor: pointer;
  text-decoration: none;
  color: #0B0D17;
}

.home__circle__text {
  font-family: 'Bellefair', serif;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  -webkit-transition: margin 0.5s ease;
  transition: margin 0.5s ease;
}
